export const round = (value : number, digits : number = 0) =>{
  const multiplier = Math.pow(10, digits);
  return Math.round(value * multiplier) / multiplier;
};

export const toFixedForNumbers = (numbers : number[]):number => {
  const decimalPartsLength = numbers.map(num => num.toString().split('.')[1]?.length ?? 0)
  return Math.max(...decimalPartsLength) // counting maximum decimal part of values
}
