import React, { FC, useEffect, useRef, useState } from 'react';
import {
  ButtonContainer,
  CalculatorDescription,
  CalculatorRow,
  CalculatorSection,
} from './styled';
import { NumberInput, Text } from '../common';
import { PrimaryButton } from '../common/Buttons/Buttons';
import { CalculatorUserForm } from './CalculatorUserForm';
import { formatPrice } from '../../utils/format';
import {
  CalculatorBlock,
  CalculatorCountingSection,
  CalculatorTotalSaveSection,
  TotalSave,
} from './styled';
import { useInView } from 'react-cool-inview';
import useWindowSize, { isDesktop } from '../../hooks/useWindowSize';
import { colors } from '../../utils/colors';
import { getCookie } from '../../utils/getCookie';

const placeHolderValues = {
  messageVolume: '5,000,000',
  platformFeeCost: '$0.005',
  messagesLost: '500,000',
  passThroughLost: '$1,500',
  monthLost: '$4,000',
  annualLost: '$48,000',
};

const SmartQueueingCalculator: FC<{ withoutDescription?: boolean }> = ({
  withoutDescription,
}) => {
  const { observe, inView } = useInView({});
  const messaginngVolumeRef = useRef<HTMLInputElement>(null);

  const [showForm, setShowForm] = useState(false);

  const [messagingVolume, setMessagingVolume] = useState('');
  const [platformFeesCost, setPlatformFeesCost] = useState('');
  const [annualRevealedLost, setAnnualRevealedLost] = useState('');
  const [monthRevealedLost, setMonthRevealedLost] = useState('');

  const [shouldValidate, setShouldValidate] = useState(false);

  const messagesLost = +messagingVolume / 10;

  const passThroughLostNumber = +messagesLost * 0.003;
  const passThroughLost = passThroughLostNumber ? passThroughLostNumber.toFixed(2) : '';

  const platformLostNumber = +platformFeesCost * messagesLost;
  const platformLost = platformLostNumber ? platformLostNumber.toFixed(2) : '';

  const monthLost = +passThroughLost + +platformLost || '';
  const annualLost = +monthLost * 12 || '';

  const isEmptyPlaceholder = Boolean(messagingVolume || platformFeesCost);

  const isMessagingVolumeValid = +messagingVolume > 0;
  const isPlatformFeeValid = +platformFeesCost > 0;

  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  const onSubmit = () => {
    const data = {
      fields: [
        {
          objectTypeId: '0-1',
          name: 'smart_queue_calculator_volume',
          value: String(messagingVolume),
        },
        {
          objectTypeId: '0-1',
          name: 'annual_potential_lost_revenue__sqc_',
          value: String(annualLost),
        },
        {
          objectTypeId: '0-1',
          name: 'monthly_potential_lost_revenue__sqc_',
          value: String(monthLost),
        },
        {
          objectTypeId: '0-1',
          name: 'smart_queueing_calculator_platform_fees_cost',
          value: String(platformFeesCost),
        },
      ],
      context: {
        hutk: getCookie('hubspotutk'),
        pageUri: document.location.href,
        pageName: document.title,
      },
    };

    fetch(
      'https://api.hsforms.com/submissions/v3/integration/submit/21630623/02cab53c-1b01-4d86-944e-4bf6bef09d58',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      },
    );

    setAnnualRevealedLost(String(annualLost));
    setMonthRevealedLost(String(monthLost));
    setShouldValidate(false);
  };

  const getAnnualLostDisplayValue = () => {
    if (annualRevealedLost) {
      return '$' + formatPrice(+annualRevealedLost);
    }

    if (isEmptyPlaceholder) {
      return '...';
    }

    return placeHolderValues.annualLost;
  };

  useEffect(() => {
    if (!isDesktopScreen) return;

    if (inView) {
      messaginngVolumeRef?.current?.focus({ preventScroll: true });
    } else {
      messaginngVolumeRef?.current?.blur();
    }
  }, [isDesktopScreen, inView]);

  useEffect(() => {
    if (annualRevealedLost || monthRevealedLost) {
      setAnnualRevealedLost('');
      setMonthRevealedLost('');
    }
  }, [messagingVolume, platformFeesCost]);

  return (
    <CalculatorSection id="smart-queueing-calculator" withoutDescription={withoutDescription}>
      <CalculatorBlock>
        <CalculatorCountingSection>
          <Text.Title.Bold.sm>Smart Queueing Calculator</Text.Title.Bold.sm>
          <Text.Regular.Bold.md>Cost of Failed Messages</Text.Regular.Bold.md>
          <CalculatorRow>
            <div>
              <Text.Regular.Normal.sm>Monthly Messaging Volume</Text.Regular.Normal.sm>
            </div>
            <div ref={observe}>
              <NumberInput
                placeholder={placeHolderValues.messageVolume}
                allowLeadingZeros={false}
                value={messagingVolume}
                decimalScale={0}
                emptyPlaceholder={isEmptyPlaceholder}
                isValid={shouldValidate ? isMessagingVolumeValid : true}
                getInputRef={messaginngVolumeRef}
                onValueChange={(valueObj) => setMessagingVolume(valueObj.value)}
                valueIsNumericString
              />
            </div>
          </CalculatorRow>
          <CalculatorRow>
            <div>
              <Text.Regular.Normal.sm> Platform Fees Cost</Text.Regular.Normal.sm>
              <Text.Regular.Normal.xs>(What You Pay Per-Message)</Text.Regular.Normal.xs>
            </div>
            <div>
              <NumberInput
                placeholder={placeHolderValues.platformFeeCost}
                prefix="$"
                value={platformFeesCost}
                decimalScale={5}
                emptyPlaceholder={isEmptyPlaceholder}
                isValid={shouldValidate ? isPlatformFeeValid : true}
                onValueChange={(valueObj) => setPlatformFeesCost(valueObj.value)}
                valueIsNumericString
              />
            </div>
          </CalculatorRow>
          <CalculatorRow>
            <div>
              <Text.Regular.Normal.sm>
                10% of Total Messages Are Expected to Drop Each Month
              </Text.Regular.Normal.sm>
            </div>
            <div>
              <NumberInput
                placeholder={placeHolderValues.messagesLost}
                disabled
                emptyPlaceholder={isEmptyPlaceholder}
                value={messagesLost ? Math.round(+messagesLost) : ''}
              />
            </div>
          </CalculatorRow>
          <CalculatorRow>
            <div>
              <Text.Regular.Normal.sm>
                $ Spent on Carrier Pass-through Fees for Failed Messages
              </Text.Regular.Normal.sm>
              <Text.Regular.Normal.xs>
                (Approx. $0.003 Per-Message)
              </Text.Regular.Normal.xs>
            </div>
            <div>
              <NumberInput
                disabled
                placeholder={placeHolderValues.passThroughLost}
                prefix="$"
                emptyPlaceholder={isEmptyPlaceholder}
                value={passThroughLost}
              />
            </div>
          </CalculatorRow>
          <ButtonContainer>
            <PrimaryButton
              onClick={() => {
                if (isMessagingVolumeValid && isPlatformFeeValid) {
                  onSubmit();
                }
                setShouldValidate(true);
              }}
            >
              Calculate
            </PrimaryButton>
          </ButtonContainer>
          <Text.Regular.Bold.md>Potential Lost Revenue</Text.Regular.Bold.md>
          <CalculatorRow>
            <div>
              <Text.Regular.Normal.sm>Monthly</Text.Regular.Normal.sm>
            </div>
            <div>
              <NumberInput
                disabled
                placeholder={placeHolderValues.monthLost}
                prefix="$"
                textColor={colors.secondary.lightRed}
                placeholderColor={colors.secondary.lightRed}
                emptyPlaceholder={isEmptyPlaceholder}
                value={monthRevealedLost}
              />
            </div>
          </CalculatorRow>
          <CalculatorRow>
            <div>
              <Text.Regular.Normal.sm>Annually</Text.Regular.Normal.sm>
            </div>
            <div>
              <NumberInput
                disabled
                placeholder={placeHolderValues.annualLost}
                textColor={colors.secondary.lightRed}
                placeholderColor={colors.secondary.lightRed}
                prefix="$"
                emptyPlaceholder={isEmptyPlaceholder}
                value={annualRevealedLost}
              />
            </div>
          </CalculatorRow>
          {/* {showForm && (
            <CalculatorUserForm
              onClose={() => setShowForm(false)}
              montlyPotentialLostRevenue={monthLost.toString()}
              annualPotentialLostRevenue={annualLost.toString()}
              platformFee={platformFeesCost}
              messagingVolume={messagingVolume}
              onSubmit={onSubmit}
            />
          )} */}
        </CalculatorCountingSection>
        <CalculatorTotalSaveSection>
          <TotalSave>
            <Text.Regular.Normal.sm>
              How much you could be saving each year with Telgorithm’s Smart Queueing
              technology
            </Text.Regular.Normal.sm>
            <Text.Title.Bold.md>{getAnnualLostDisplayValue()}</Text.Title.Bold.md>
          </TotalSave>
        </CalculatorTotalSaveSection>
      </CalculatorBlock>
      {!withoutDescription && (
        <CalculatorDescription>
          <div>
            <Text.Title.Bold.lg>How much $$ would you save?</Text.Title.Bold.lg>
          </div>
          <div>
            <Text.Regular.Bold.md>
              An estimated 10% of all 10DLC text messages are failing each month due to
              exceeding Carrier rate limits.
            </Text.Regular.Bold.md>

            <Text.Regular.Normal.sm>
              Use our calculator to find out how much your team could be saving annually
              with Smart Queueing.
            </Text.Regular.Normal.sm>

            <Text.Regular.Normal.sm>
              Enter your estimated monthly messaging volume (the number of 10DLC messages
              your application or software sends each month), your cost per message, and
              we’ll do the rest.
            </Text.Regular.Normal.sm>
          </div>
        </CalculatorDescription>
      )}
    </CalculatorSection>
  );
};

export default SmartQueueingCalculator;
