import {round, toFixedForNumbers} from "./round";

const formatNumber = (value : number, digits : number) => {
  if (value < 1_000) {
    return round(value, digits).toString();
  }

  if (value < 1_000_000) {
    return `${round(value / 1_000, digits)}K`;
  }

  if (value < 1_000_000_000) {
    return `${round(value / 1_000_000, digits)}M`;
  }

  return `${round(value / 1_000_000_000, digits)}B`;
}

export const formatPrice = (value : number) : string => {
  // Round to 3 digits if number less than 100
  if (value < 100) {
    const valueRounded = Math.round((value + Number.EPSILON) * 1000) / 1000;
    return valueRounded.toFixed(toFixedForNumbers([valueRounded])).toString();
  }

  // Round to 1 digit if number less than 10_000
  if (value < 10_000) {
    return round(value, 1).toString();
  }

  // Round to 1 digit for others
  return formatNumber(value, 1);
}

export const formatLimit = (value: number) =>
  formatNumber(value, 0);

const formatter = new Intl.NumberFormat('en-US', {
    notation: 'compact',
    maximumFractionDigits: 1,
  });
  
export const formatNumberToCompactString = (sourceNumber: number) =>
    formatter.format(sourceNumber);
