export const StyledSmartQueueingSchema = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: column;
  display: grid;
  grid-auto-columns: minmax(0, 1fr);
  grid-auto-flow: column;
  gap: 20px;
  grid-template-areas:
    'schema'
    'step_1'
    'step_2'
    'step_3'
    'step_4'
    'step_5';

  ${StyledSmartQueueingStep}:first-child {
    @media ${device.laptop} {
      left: 20px;
      top: 40px;
    }
    grid-area: step_1;
  }

  ${StyledSmartQueueingStep}:nth-child(2) {
    grid-area: step_2;
    @media ${device.laptop} {
      left: 200px;
      bottom: 50px;
    }
  }

  ${StyledSmartQueueingStep}:nth-child(3) {
    grid-area: step_3;
    @media ${device.laptop} {
      left: 500px;
      top: 0px;
    }
  }

  ${StyledSmartQueueingStep}:nth-child(4) {
    grid-area: step_4;
    @media ${device.laptop} {
      right: 200px;
      bottom: 50px;
    }
  }

  ${StyledSmartQueueingStep}:nth-child(5) {
    grid-area: step_5;
    @media ${device.laptop} {
      right: 20px;
      top: 80px;
    }
  }

  svg {
    grid-area: schema;
    max-width: 100%;
    margin: 0 auto;
  }

  @media ${device.laptop} {
    display: flex;
    padding: 200px 200px;
    flex-direction: row;
    transform: scale(0.9);
    svg {
      max-width: unset;
    }
    ${StyledSmartQueueingStep} {
      position: absolute;
    }
  }

  @media ${device.desktop} {
    transform: none;
  }
`;

